import {
    AUTH_SUCCESS,
    ERROR_MSG,
    RECEIVE_MSG,
    RECEIVE_MSG_LIST,
    RECEIVE_USER,
    RECEIVE_USER_LIST,
    RESET_USER,
    MSG_READ
} from './action-types'


//Ê≥®ÂÜåÂíåÁôªÂΩïÊòØÂºÇÊ≠•ÁöÑÔºåÊâÄ‰ª•Ë¶ÅÂàõÂª∫ÂºÇÊ≠•action
import {
    reqRegister,
    reqLogin,
    reqUpdataUser,
    reqUser,
    reqUserList,
    reqChatMsgList,
    reqReadMsg
} from '../api'
//ÂºïÂÖ•ÂÆ¢Êà∑Á´Øio
import { io } from 'socket.io-client'

function initIO(dispatch, userid) {
    // 1ÂàõÂª∫ÂØπË±°‰πãÂâçÔºöÂà§Êñ≠ÂØπË±°ÊòØÂê¶Â∑≤ÁªèÂàõÂª∫ÔºåÊ≤°ÂàõÂª∫ÊâçÂàõÂª∫
    if (!io.socket) {
        //ËøûÊé•ÊúçÂä°Âô®ÔºåÂæóÂà∞‰∏éÊúçÂä°Âô®ÁöÑËøûÊé•ÂØπË±°
        io.socket = io("ws://localhost:3003")
        io.socket.on('receiveMsg', function (chatMsg) {
            console.log("ÂÆ¢Êà∑Á´ØÊé•Êî∂Âà∞ÁöÑÊ∂àÊÅØ", chatMsg)
            //Âè™ÊúâÂΩìchatMsgÊòØ‰∏éÂΩìÂâçÁî®Êà∑Áõ∏ÂÖ≥ÁöÑÊ∂àÊÅØÔºåÊâç‰ºöÂéªÂàÜÂèëÂêåÊ≠•action
            if (userid === chatMsg.from || userid === chatMsg.to) {
                dispatch(receiveMsg(chatMsg, userid))
            }

        })
    }
}

//ÊéàÊùÉÊàêÂäüÁöÑÂêåÊ≠•action
const authSuccess = (user) => ({ type: AUTH_SUCCESS, data: user })
//ÈîôËØØÊèêÁ§∫ÁöÑÂêåÊ≠•action
const errorMsg = (msg) => ({ type: ERROR_MSG, data: msg })
//Êé•Êî∂Áî®Êà∑ÁöÑÂêåÊ≠•action
const receiveUser = (user) => ({ type: RECEIVE_USER, data: user })
//ÈáçÁΩÆÁî®Êà∑ÁöÑÂêåÊ≠•action
export const resetUser = (msg) => ({ type: RESET_USER, data: msg })
//Ëé∑ÂèñÁî®Êà∑ÂàóË°®ÁöÑÂêåÊ≠•action
const receiveUserList = (users) => ({ type: RECEIVE_USER_LIST, data: users })
//Ëé∑ÂèñÊ∂àÊÅØÂàóË°®ÁöÑÂêåÊ≠•action
const receiveMsgList = ({ users, chatMsgs, userid }) => ({ type: RECEIVE_MSG_LIST, data: { users, chatMsgs, userid } })
//Êé•Êî∂‰∏Ä‰∏™Ê∂àÊÅØÁöÑÂêåÊ≠•action  
const receiveMsg = (chatMsg, userid) => ({ type: RECEIVE_MSG, data: { chatMsg, userid } })
//ËØªÂèñ‰∫ÜÊüê‰∏™ËÅäÂ§©Ê∂àÊÅØÁöÑÂêåÊ≠•action
const msgRead = ({ count, from, to }) => ({ type: MSG_READ, data: { count, from, to } })

//ÂºÇÊ≠•Ëé∑ÂèñÊ∂àÊÅØÂàóË°®Êï∞ÊçÆ
async function getMsgList(dispatch, userid) {
    initIO(dispatch, userid)
    const response = await reqChatMsgList()
    const result = response.data
    if (result.code === 0) {
        const { users, chatMsgs } = result.data
        dispatch(receiveMsgList({ users, chatMsgs, userid }))
    }
}
//Ê≥®ÂÜåÂºÇÊ≠•action
export const register = (user) => {
    const { username, password, password2, type } = user
    //ÂÅöË°®ÂçïÁöÑÂâçÂè∞Ê£ÄÊü•ÔºåÂ¶ÇÊûú‰∏çÈÄöËøáÔºåËøîÂõû‰∏Ä‰∏™errorMsgÁöÑÂêåÊ≠•action
    if (!username) {
        return errorMsg("Áî®Êà∑ÂêçÂøÖÈ°ªÊåáÂÆö")
    }
    else if (password !== password2) {
        return errorMsg("‰∏§Ê¨°ÂØÜÁ†Å‰∏ç‰∏ÄËá¥")
    }
    //Ë°®ÂçïÊï∞ÊçÆÂêàÊ≥ïÔºåËøîÂõû‰∏Ä‰∏™ÂèëajaxËØ∑Ê±ÇÁöÑÂºÇÊ≠•action
    //ÂºÇÊ≠•actionËøîÂõûÁöÑÊòØ‰∏Ä‰∏™ÂáΩÊï∞
    return async dispatch => {
        let result = await reqRegister({ username, password, type })
        result = result.data
        console.log(result)
        if (result.code === 0) {
            getMsgList(dispatch, result.data._id)
            dispatch(authSuccess(result.data))
        } else {
            dispatch(errorMsg(result.msg))
        }
    }
}

//ÁôªÂΩïÂºÇÊ≠•action
export const login = (user) => {
    console.log(user)
    const { username, password } = user
    //ÂÅöË°®ÂçïÁöÑÂâçÂè∞Ê£ÄÊü•ÔºåÂ¶ÇÊûú‰∏çÈÄöËøáÔºåËøîÂõû‰∏Ä‰∏™errorMsgÁöÑÂêåÊ≠•action
    if (!username) {
        return errorMsg("Áî®Êà∑ÂêçÂøÖÈ°ªÊåáÂÆö")
    }
    else if (!password) {
        return errorMsg("ÂØÜÁ†ÅÂøÖÈ°ªÊåáÂÆö")
    }
    //Ë°®ÂçïÊï∞ÊçÆÂêàÊ≥ïÔºåËøîÂõû‰∏Ä‰∏™ÂèëajaxËØ∑Ê±ÇÁöÑÂºÇÊ≠•action
    //ÂºÇÊ≠•actionËøîÂõûÁöÑÊòØ‰∏Ä‰∏™ÂáΩÊï∞
    return async dispatch => {
        const response = await reqLogin(user)
        console.log(response)
        const result = response.data
        if (result.code === 0) {
            getMsgList(dispatch, result.data._id)
            console.log(result.data)
            dispatch(authSuccess(result.data))
        } else {
            dispatch(errorMsg(result.msg))
        }
    }
}

//Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØ
export const updateUser = (user) => {
    return async dispatch => {
        const response = await reqUpdataUser(user)
        const result = response.data
        if (result.code === 0) {
            dispatch(receiveUser(result.data))
        } else {
            dispatch(resetUser(result.msg))
        }
    }
}

//Ëé∑ÂèñÁî®Êà∑ÂºÇÊ≠•action
export const getUser = () => {
    return async dispatch => {
        const response = await reqUser()
        const result = response.data
        if (result.code === 0) {
            getMsgList(dispatch, result.data._id)
            dispatch(receiveUser(result.data))
        } else {
            dispatch(resetUser(result.msg))
        }
    }
}

//Ëé∑ÂèñÁî®Êà∑ÂàóË°®ÁöÑÂºÇÊ≠•action
export const getUserList = (type) => {
    return async dispatch => {
        const response = await reqUserList(type);
        const result = response.data
        if (result.code === 0) {
            dispatch(receiveUserList(result.data))
        }
    }
}

//ÂèëÈÄÅÊ∂àÊÅØÁöÑÂºÇÊ≠•action
export const sendMsg = ({ from, to, content }) => {
    return async dispatch => {

        io.socket.emit('sendMsg', { from, to, content })
    }
}

//ËØªÂèñÊ∂àÊÅØÁöÑÂºÇÊ≠•action
export const readMsg = (from, to) => {
    return async dispatch => {
        const response = await reqReadMsg(from)
        const result = response.data
        const count = result.data
        console.log(count)
        console.log(from)
        console.log(to)
        if (result.code === 0) {
            dispatch(msgRead({ count, from, to }))
        }
    }
}