// ÂåÖÂê´n‰∏™reducersÂáΩÊï∞ÔºöÊ†πÊçÆËÄÅÁöÑstateÂíåÊåáÂÆöÁöÑactionËøîÂõû‰∏Ä‰∏™Êñ∞ÁöÑstate

//ÂêàÂπ∂reducer
import { combineReducers } from "redux";
// import laobanInfo from "../containers/laoban-info/laoban-info";
import {
    AUTH_SUCCESS,
    ERROR_MSG,
    RESET_USER,
    RECEIVE_USER,
    RECEIVE_USER_LIST,
    RECEIVE_MSG_LIST,
    RECEIVE_MSG,
    MSG_READ
} from './action-types'
import { getRedirectTo } from "../utils";
const initUser = {
    username: '',
    type: '',
    msg: '',
    redirectTo: ''//ÈúÄË¶ÅÈáçÂÆöÂêëÁöÑË∑ØÂæÑ
}
const initUserList = []
const initChat = {
    users: {},
    chatMsgs: [],
    unReadCount: 0 //ÊÄªÁöÑÊú™ËØªÊï∞Èáè
}
//‰∫ßÁîüuserÁöÑreducer
function user(state = initUser, action) {
    switch (action.type) {
        case AUTH_SUCCESS:
            const { type, header } = action.data
            return { ...action.data, redirectTo: getRedirectTo(type, header) }
        case ERROR_MSG:
            return { ...state, msg: action.data }
        case RECEIVE_USER:
            return action.data
        case RESET_USER:
            return { ...initUser, msg: action.data }
        default: return state
    }

}

//‰∫ßÁîüuserlistÁöÑreducer
function userList(state = initUserList, action) {
    switch (action.type) {
        case RECEIVE_USER_LIST:
            return action.data
        default:
            return state
    }
}

//‰∫ßÁîü‰∏éÂΩìÂâçÁî®Êà∑ÊúâÂÖ≥ÁöÑËÅäÂ§©
function chat(state = initChat, action) {
    switch (action.type) {
        case RECEIVE_MSG_LIST:
            const { users, chatMsgs, userid } = action.data
            return {
                users,
                chatMsgs,
                unReadCount: chatMsgs.reduce((preTotal, msg) => preTotal + (!msg.read && msg.to === userid ? 1 : 0), 0)
            }
        case RECEIVE_MSG:
            const { chatMsg } = action.data
            return {
                users: state.users,
                chatMsgs: [...state.chatMsgs, chatMsg],
                unReadCount: state.unReadCount + (!chatMsg.read && chatMsg.to === action.data.userid ? 1 : 0)
            }
        case MSG_READ:
            const { count, from, to } = action.data

            return {
                users: state.users,
                chatMsgs: state.chatMsgs.map(msg => {
                    if (msg.from === from && msg.to === to) {
                        return { ...msg, read: true }
                    }
                    return msg
                }),
                unReadCount: state.unReadCount - count
            }
        default: return state
    }
}
// redirectToÊúâÂõõÁßçÊÉÖÂÜµ
//  /dashen  /laoban /dasheninfo /laobanInfo
// Áî®headerÂà§Êñ≠Áî®‰∏çÁî®Âéª‰ø°ÊÅØÂÆåÂñÑÈ°µÈù¢
// Áî®typeÂà§Êñ≠Áî®Êà∑Á±ªÂûã
// function getRedirectTo(type, header) {
//     let path = ''
//     if (type === 'laoban') {
//         path = '/laoban'
//     } else {
//         path = '/dashen'
//     }

//     if (!header) {
//         path += 'info'
//     }
//     return path
// }
// function yyy(state = 0, action) {
//     return state

// }

export default combineReducers({
    user,
    userList,
    chat
})
//ÂêëÂ§ñÊö¥ÊºèÁöÑÁä∂ÊÄÅÁªìÊûÑ‰∏∫Ôºö{user:{},userList:[]}