import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Badge, List } from 'antd-mobile';
import './message.css'
const Item = List.Item
const Brief = List.Item.Brief
//ÂØπchatMsgÊåâchat_idËøõË°åÂàÜÁªÑÔºåÂπ∂ÂæóÂà∞ÊØè‰∏™ÁªÑÁöÑlastMsgÁªÑÊàêÁöÑÊï∞ÁªÑ
// 1ÊâæÂá∫ÊØè‰∏™ËÅäÂ§©ÁöÑLastMsg,Âπ∂Áî®‰∏Ä‰∏™ÂØπË±°ÂÆπÂô®‰øùÂ≠ò{chat_id,lastMsg}
// 2ÂæóÂà∞ÊâÄÊúâLastMsgÁöÑÊï∞ÁªÑ
// 3ÂØπÊï∞ÁªÑËøõË°åÊéíÂ∫è(Êåâcreat_timeÈôçÂ∫è)
function getLastMsgs(chatMsgs, userid) {
    let lastMsgObjs = {}
    chatMsgs.forEach(element => {
        if (element.to === userid && !element.read) {
            element.unReadCount = 1
        } else {
            element.unReadCount = 0
        }

        if (lastMsgObjs[element.chat_id]) {
            //‰øùÂ≠òÂ∑≤ÁªèÁªüËÆ°ÁöÑÊú™ËØªÊï∞Èáè
            let preUnReadCount = lastMsgObjs[element.chat_id].unReadCount
            if (lastMsgObjs[element.chat_id].create_time < element.create_time) {
                lastMsgObjs[element.chat_id] = element
            }
            //Á¥ØÂä†
            lastMsgObjs[element.chat_id].unReadCount = preUnReadCount + element.unReadCount

        } else {
            lastMsgObjs[element.chat_id] = element
        }

    });
    //ÂæóÂà∞ÊâÄÊúâÁöÑLastMsgÊï∞ÁªÑ
    const lastMsgs = Object.values(lastMsgObjs)
    //ÊéíÂ∫è
    lastMsgs.sort(function (m1, m2) {  //Â¶ÇÊûúÂ∞è‰∫éÈõ∂ÔºåÂ∞Üm1ÊîæÂâçÈù¢
        return m2.create_time - m1.create_time

    })
    return lastMsgs
}
class Message extends Component {

    render() {
        const { user } = this.props
        const { users, chatMsgs } = this.props.chat
        //ÂØπchatMsgsÊåâchat_idËøõË°åÂàÜÁªÑ
        const lastMsgs = getLastMsgs(chatMsgs, user._id)
        return (
            <List style={{ marginTop: 50, marginBottom: 50 }}>
                {lastMsgs.map(msg => {
                    const targetUserId = msg.to === user._id ? msg.from : msg.to
                    const targetUser = msg.to === user._id ? users[msg.from] : users[msg.to]
                    const header = targetUser.header ? `/images/${targetUser.header}.jpg` : null
                    console.log(targetUser)
                    return (
                        <Item
                            key={msg._id}
                            extra={<Badge text={msg.unReadCount} />}
                            thumb={<div className='messageHeader'><img src={header} /></div>}
                            arrow='horizontal'
                            onClick={() => this.props.history.push(`/chat/${targetUserId}`)}
                        >
                            {users[msg.to === user._id ? msg.from : msg.to].username}
                            <Brief>
                                {msg.content}

                            </Brief>
                        </Item>
                    )
                }

                )}
            </List>
        )
    }
}
export default connect(
    state => ({
        user: state.user,
        chat: state.chat
    }),
    {}
)(Message)
