import React, { Component } from 'react'
import { connect } from 'react-redux'
import { NavBar, InputItem, TextareaItem, Button } from 'antd-mobile'
import HeaderSelector from '../../components/header-selector/header-selector'
import { updateUser } from '../../redux/action'
import { Redirect } from 'react-router-dom'
class DashenInfo extends Component {
    state = {
        header: '',
        post: '',
        info: '',

    }
    save = () => {
        this.props.updateUser(this.state)
    }
    setHeader = (header) => {
        this.setState({
            header
        })

    }
    handleChange = (name, value) => {
        this.setState({
            [name]: value
        })

    }
    render() {
        const { header, type } = this.props.user
        if (header) {  //‰ø°ÊÅØÂ∑≤ÁªèÂÆåÂñÑ
            const path = type === 'dashen' ? '/dashen' : 'laoban'
            return <Redirect to={path} />
        }
        return (
            <div>
                <NavBar>Â§ßÁ•û‰ø°ÊÅØÂÆåÂñÑ</NavBar>
                <HeaderSelector setHeader={this.setHeader} />
                <InputItem placeholder="ËØ∑ËæìÂÖ•Ê±ÇËÅåÂ≤ó‰Ωç" onChange={val => { this.handleChange('post', val) }}>Ê±ÇËÅåÂ≤ó‰Ωç</InputItem>
                <TextareaItem title="‰∏™‰∫∫‰ªãÁªç:" rows={3} onChange={val => { this.handleChange('info', val) }}></TextareaItem>



                <Button type="primary" onClick={this.save}>‰øù&nbsp;&nbsp;&nbsp;&nbsp;Â≠ò</Button>
            </div>
        )
    }
}
export default connect(
    state => ({ user: state.user }),
    { updateUser }
)(DashenInfo)