import React, { Component } from 'react'
import Logo from '../../components/logo/logo'
import {
    NavBar,
    WingBlank,
    List,
    InputItem,
    WhiteSpace,
    Radio,
    Button,
} from 'antd-mobile'

import { register } from '../../redux/action'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
const ListItem = List.Item



class Register extends Component {
    state = {
        username: '',
        password: '',
        password2: '',
        type: '',
    }
    register = () => {

        this.props.register(this.state)
    }
    handleChange = (name, val) => {
        //Êõ¥Êñ∞Áä∂ÊÄÅ
        //Âõ†‰∏∫ËøôÈáå‰º†ÁöÑnameÁöÑÂ≠óÁ¨¶‰∏≤ÔºåÊâÄ‰ª•Ë¶ÅÁî®[]Êé•
        this.setState({
            [name]: val
        })
    }
    //Ë∑≥ËΩ¨ÁôªÂΩï
    toLogin = () => {
        this.props.history.push('/login')

    }
    render() {
        const { type } = this.state
        const { msg, redirectTo } = this.props.user
        console.log(redirectTo)
        if (redirectTo) {
            return <Redirect to={redirectTo} />
        }

        return (
            <div>
                <NavBar>Âπø&nbsp;Â∑•&nbsp;Áõ¥&nbsp;ËÅò</NavBar>
                <Logo></Logo>
                <WingBlank>
                    <List>
                        {msg ? <div className="error-msg">{msg}</div> : null}
                        <WhiteSpace />
                        <InputItem placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç" onChange={val => { this.handleChange('username', val) }}>Áî®Êà∑Âêç:</InputItem>
                        <WhiteSpace />
                        <InputItem placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å" type="password" onChange={val => { this.handleChange('password', val) }}>ÂØÜ&nbsp;&nbsp;&nbsp;Á†Å:</InputItem>
                        <WhiteSpace />
                        <InputItem placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å" type="password" onChange={val => { this.handleChange('password2', val) }}>Á°ÆËÆ§ÂØÜÁ†Å:</InputItem>
                        <WhiteSpace />
                        <ListItem>
                            <span>Áî®Êà∑Á±ªÂûã:</span>
                            &nbsp;&nbsp;&nbsp;
                            <Radio checked={type === "dashen"} onChange={() => this.handleChange('type', 'dashen')} >Â§ßÁ•û</Radio>
                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            <Radio checked={type === "laoban"} onChange={() => this.handleChange('type', 'laoban')}>ËÄÅÊùø</Radio>
                        </ListItem>
                        <WhiteSpace />
                        <Button type="primary" onClick={this.register}>Ê≥®&nbsp;&nbsp;&nbsp;ÂÜå</Button>
                        <WhiteSpace />
                        <Button onClick={this.toLogin}>Â∑≤ÊúâË¥¶Êà∑</Button>
                    </List>
                </WingBlank>
            </div>
        )
    }
}

export default connect(
    state => ({ user: state.user }),
    { register }
)(Register)