import React, { Component } from 'react'
import { connect } from 'react-redux'
import { InputItem, List, NavBar, Grid, Icon } from 'antd-mobile'

import { sendMsg, readMsg } from '../../redux/action'
const Item = List.Item
class Chat extends Component {
    state = {
        connect: "",
        isShow: false, //ÊòØÂê¶ÊòæÁ§∫Ë°®ÊÉÖÂàóË°®
    }

    handleSend = () => {
        //Êî∂ÈõÜÊï∞ÊçÆ
        const from = this.props.user._id
        const to = this.props.match.params.userid
        const content = this.state.content.trim()
        //ÂèëÈÄÅÊ∂àÊÅØ
        if (content) {
            console.log(from)
            console.log(to)
            console.log(content)
            this.props.sendMsg({ from, to, content })
            this.setState({ content: '', isShow: false })
        }
    }
    toggleShow = () => {
        const isShow = !this.state.isShow
        this.setState({ isShow })
        if (isShow) {
            setTimeout(() => {
                window.dispatchEvent(new Event('resize'))
            }, 0)
        }
    }
    //ÈÄâÊã©Ë°®ÊÉÖ
    chooseEmoji = (item) => {

        if (this.state.content) {
            this.setState({ content: this.state.content + item.text })
        } else {
            this.setState({ content: item.text })
        }

    }
    //Âú®Á¨¨‰∏ÄÊ¨°render‰πãÂâçË∞ÉÁî®
    componentWillMount() {
        //ÂàùÂßãÂåñË°®ÊÉÖÂàóË°®Êï∞ÊçÆ
        const emojis = ["üòÄ", "üòÉ", "üòÑ", "üòÅ", "üòÜ", "ü§£", "üòÇ", "üôÇ", "üòâ", "üòä", "üòá", "ü•∞", "üòç", "ü§©", "üòò", "üòó", "üòö", "üòô", "üòã", "üòõ", "üòú", "ü§™", "üòù", "ü§ë", "ü§ó", "ü§≠", "ü§´", "ü§î", "ü§ê", "ü§®", "üòê", "üòë", "üò∂", "üò∂", "üòè", "üòí", "üôÑ", "üò¨"]
        this.emojis = emojis.map(emoji => ({ text: emoji }))
    }

    componentDidMount() {
        window.scrollTo(0, document.body.scrollHeight)

    }
    componentDidUpdate() {
        window.scrollTo(0, document.body.scrollHeight)
    }
    componentWillUnmount() {
        //ÂèëËØ∑Ê±ÇÊõ¥Êñ∞Ê∂àÊÅØÁöÑÊú™ËØª
        const from = this.props.match.params.userid
        const to = this.props.user._id
        this.props.readMsg(from, to)
    }
    render() {
        const { user } = this.props
        const { users, chatMsgs } = this.props.chat

        //ËÆ°ÁÆóÂΩìÂâçËÅäÂ§©ÁöÑchat_id
        const meId = user._id
        const targetId = this.props.match.params.userid

        //Â¶ÇÊûúËøòÊ≤°Ëé∑ÂèñÂà∞Êï∞ÊçÆÔºå‰∏çÂÅö‰ªª‰ΩïÊòæÁ§∫
        if (!users[meId]) {
            return null
        }
        const chatId = [meId, targetId].sort().join('_')
        const msgs = chatMsgs.filter(val => val.chat_id === chatId)

        const header = users[targetId].header ? `/images/${users[targetId].header}.jpg` : null
        const targetName = users[targetId].username

        return (
            <div id="chat-page">
                <NavBar icon={<Icon type='left' />}
                    className='sticky-header'
                    onLeftClick={() => { this.props.history.goBack() }}
                >{targetName}</NavBar>

                <List style={{ marginTop: 50, marginBottom: 50 }}>

                    {msgs.map(msg => {
                        if (meId === msg.to) {
                            return (
                                <Item
                                    key={msg._id}
                                    thumb={<div className="header2"><img src={header} /></div>}
                                >
                                    {msg.content}
                                </Item>
                            )
                        } else {
                            return (
                                <Item
                                    key={msg._id}
                                    className="chat-me"
                                    extra='Êàë'
                                >
                                    {msg.content}
                                </Item>
                            )
                        }
                    })}

                </List>

                <div className="am-tab-bar">
                    <InputItem
                        placeholder="ËØ∑ËæìÂÖ•"
                        onChange={val => this.setState({ content: val })}
                        value={this.state.content}
                        onFocus={() => { this.setState({ isShow: false }) }}
                        extra={
                            <span>
                                <span onClick={this.toggleShow} style={{ marginRight: 5 }}>üôÇ</span>
                                <span onClick={this.handleSend}>ÂèëÈÄÅ</span>

                            </span>
                        }>
                    </InputItem>

                    {this.state.isShow ? (<Grid
                        data={this.emojis}
                        columnNum={8}
                        carouselMaxRow={4}
                        isCarousel={true}
                        onClick={(item) => {
                            this.chooseEmoji(item)
                        }}
                    ></Grid>) : null}
                </div>
            </div>
        )
    }
}
export default connect(
    state => ({ user: state.user, chat: state.chat }),
    { sendMsg, readMsg }
)
    (Chat)
