import React, { Component } from 'react'
import DashenInfo from '../dashen-info/dashen-info'
import LaobanInfo from '../laoban-info/laoban-info'
import Laoban from '../laoban/laoban'
import Dashen from '../dashen/dashen'
import Message from '../message/message'
import Personal from '../personal/personal'
import { Route, Switch } from 'react-router-dom'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import Cookies from 'js-cookie' //set() get() remove()
import { getRedirectTo } from '../../utils/index'
import { getUser } from '../../redux/action'
import { NavBar } from 'antd-mobile'
import NavFooter from '../../components/nav-footer/nav-footer'
import Chat from '../../containers/chat/chat'
class Main extends Component {
    navList = [
        {
            path: '/laoban',
            component: Laoban,
            title: 'Â§ßÁ•ûÂàóË°®',
            icon: 'dashen',
            text: 'Â§ßÁ•û'
        },
        {
            path: '/dashen',
            component: Dashen,
            title: 'ËÄÅÊùøÂàóË°®',
            icon: 'laoban',
            text: 'ËÄÅÊùø'
        },
        {
            path: '/message',
            component: Message,
            title: 'Ê∂àÊÅØÂàóË°®',
            icon: 'message',
            text: 'Ê∂àÊÅØ'
        },
        {
            path: '/personal',
            component: Personal,
            title: 'Áî®Êà∑‰∏≠ÂøÉ',
            icon: 'personal',
            text: '‰∏™‰∫∫'
        },

    ]
    componentDidMount() {
        //  1.1Â¶ÇÊûúcookie‰∏≠Êúâuserid‰ΩÜredux‰∏≠Ê≤°ÊúâÔºàÂç≥ÂÖ≥Èó≠ËøáÊµèËßàÂô®Ôºâ,ÂèëËØ∑Ê±ÇËé∑ÂèñÂØπÂ∫îÁöÑuser
        const userid = Cookies.get('userid')
        const { id } = this.props.user
        if (userid && !id) {
            //ÂèëÈÄÅÂºÇÊ≠•ËØ∑Ê±ÇÔºåËé∑Âèñuser
            this.props.getUser()
        }
    }
    render() {
        //render‰∏≠‰∏çËÉΩÊâßË°åÂºÇÊ≠•
        //Á¨¨‰∏ÄÊ¨°ÊâßË°årenderÂêé‰ºöÊâßË°åcomponentDidMount
        //Ê£ÄÊü•ÊòØÂê¶ÁôªÂΩïÁöÑÊìç‰ΩúÊîæÂú®main,ÊòØÂõ†‰∏∫mainÊòØÁî®Êà∑‰∏ªÈ°µÂíåÁî®Êà∑ÂÆåÂñÑÈ°µÁöÑ‰∏äÁ∫ßË∑ØÁî±ÔºåÂèØ‰ª•‰∏ÄÂä≥Ê∞∏ÈÄ∏
        // if (!this.props.user._id) {
        //     return <Redirect to={'/login'} />
        // }

        //ËØªÂèñcookie‰∏≠ÁöÑuserid
        const userid = Cookies.get('userid')
        //Â¶ÇÊûúÊ≤°ÊúâÔºåËá™Âä®ÈáçÂÆöÂêëÂà∞ÁôªÈôÜÁïåÈù¢
        // debugger
        if (!userid) {
            return <Redirect to='/login' />
        }
        //Â¶ÇÊûúÊúâÔºåËØªÂèñredux‰∏≠ÁöÑuser
        const { user, unReadCount } = this.props
        //Â¶ÇÊûúredux‰∏≠ÁöÑuserÊúâ_id,ÊòæÁ§∫ÂØπÂ∫îÁöÑÁïåÈù¢
        if (user._id) {
            //Â¶ÇÊûúËØ∑Ê±ÇÁöÑÊòØÊ†πË∑ØÂæÑÔºåÊ†πÊçÆuserÁöÑtypeÂíåheaderÊù•ËÆ°ÁÆóÂá∫‰∏Ä‰∏™ÈáçÂÆöÂêëÁöÑË∑ØÁî±Ë∑ØÂæÑ
            let path = this.props.location.pathname
            if (path === '/') {

                path = getRedirectTo(user.type, user.header)
                return <Redirect to={path} />
            }
        } else {
            return null
        }

        const { navList } = this
        const path = this.props.location.pathname
        const currentNav = navList.find(nav => nav.path === path)

        if (currentNav) {
            //ÂÜ≥ÂÆöÂì™‰∏™Ë∑ØÁî±ÈúÄË¶ÅÈöêËóè
            if (user.type === 'laoban') {
                navList[1].hide = true
            } else {
                navList[0].hide = true
            }
        }
        return (


            <div>
                {currentNav ? <NavBar className="sticky-header">{currentNav.title}</NavBar> : null}
                <Switch>
                    <Route path="/laoban" component={(props) => <Laoban {...props} />}></Route>
                    <Route path="/dashen" component={(props) => <Dashen {...props} />}></Route>
                    <Route path="/personal" component={(props) => <Personal {...props} />}></Route>
                    <Route path="/message" component={(props) => <Message {...props} />}></Route>
                    <Route path="/laobaninfo" component={(props) => <LaobanInfo {...props} />}></Route>
                    <Route path="/dasheninfo" component={(props) => <DashenInfo {...props} />}></Route>
                    <Route path="/chat/:userid" component={(props) => <Chat {...props} />}></Route>


                </Switch>
                {currentNav ? <NavFooter navList={navList} unReadCount={unReadCount}></NavFooter> : null}
            </div>
        )
    }
}

export default connect(
    state => ({ user: state.user, unReadCount: state.chat.unReadCount }),
    { getUser }
)(Main)

// 1.ÂÆûÁé∞Ëá™Âä®ÁôªÂΩï
//  1.1Â¶ÇÊûúcookie‰∏≠Êúâuserid‰ΩÜredux‰∏≠Ê≤°ÊúâÔºàÂç≥ÂÖ≥Èó≠ËøáÊµèËßàÂô®Ôºâ,ÂèëËØ∑Ê±ÇËé∑ÂèñÂØπÂ∫îÁöÑuser
//  1.2Â¶ÇÊûúcookie‰∏≠Ê≤°Êúâuserid,Ëá™Âä®ËøõÂÖ•LoginÁïåÈù¢
// 2.Â¶ÇÊûúÂ∑≤ÁªèÁôªÂΩï,ÈáçÂÆöÂêëËØ∑Ê±ÇË∑ØÂæÑ
//  2.1 Ê†πÊçÆuserÁöÑtypeÂíåheaderÊù•ËÆ°ÁÆó‰∏Ä‰∏™ÈáçÂÆöÂêëË∑ØÂæÑÔºåÂπ∂Ëá™Âä®ÈáçÂÆöÂêë