import React, { Component } from 'react'
import Logo from '../../components/logo/logo'
import {
    NavBar,
    WingBlank,
    List,
    InputItem,
    WhiteSpace,

    Button,
} from 'antd-mobile'
import { connect } from 'react-redux'
import { login } from '../../redux/action'
import { Redirect } from 'react-router-dom'
// const ListItem = List.Item



class Login extends Component {
    state = {
        username: '',
        password: '',

    }
    login = () => {

        this.props.login(this.state)
    }
    handleChange = (name, val) => {
        //Êõ¥Êñ∞Áä∂ÊÄÅ
        //Âõ†‰∏∫ËøôÈáå‰º†ÁöÑnameÁöÑÂ≠óÁ¨¶‰∏≤ÔºåÊâÄ‰ª•Ë¶ÅÁî®[]Êé•
        this.setState({
            [name]: val
        })
    }
    //Ë∑≥ËΩ¨ÁôªÂΩï
    toRegister = () => {
        this.props.history.push('/register')

    }
    render() {

        // const { type } = this.state
        const { msg, redirectTo } = this.props.user

        if (redirectTo) {
            return <Redirect to={redirectTo} />
        }
        return (
            <div>
                <NavBar>Âπø&nbsp;Â∑•&nbsp;Áõ¥&nbsp;ËÅò</NavBar>
                <Logo></Logo>
                <WingBlank>
                    <List>
                        {msg ? <div className="error-msg">{msg}</div> : null}
                        <WhiteSpace />
                        <InputItem placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç" onChange={val => { this.handleChange('username', val) }}>Áî®Êà∑Âêç:</InputItem>
                        <WhiteSpace />
                        <InputItem placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å" type="password" onChange={val => { this.handleChange('password', val) }}>ÂØÜ&nbsp;&nbsp;&nbsp;Á†Å:</InputItem>
                        <WhiteSpace />

                        <Button type="primary" onClick={this.login}>Áôª&nbsp;&nbsp;&nbsp;ÂΩï</Button>
                        <WhiteSpace />
                        <Button onClick={this.toRegister}>ÂéªÊ≥®ÂÜå</Button>
                    </List>
                </WingBlank>
            </div>
        )
    }
}

export default connect(
    state => ({ user: state.user }),
    { login }
)(Login)
